.icon{
    background: rgba(255, 172, 172, .3);
    border: 0;
    border-radius: 100px;
    cursor: pointer;
    margin-left: 150px;
}

.icon:hover{
    background: rgba(255,172,172,.8);
}

.icon span{
    display: block;
    width: 40px;
    font-size: 30px;
    
}

